@import url(https://fonts.googleapis.com/css?family=Anton);
body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

ol, ul {
  padding-left: 30px;
}

.ol-reverse {
  display: flex;
  flex-direction: column-reverse
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}
h1{
  text-align: center;
  margin: 5px auto;   
  font-family: 'Anton', sans-serif;
  font-style: italic;
  color:red; 
  font-size: 300%;
    text-shadow: 2px 2px 2px #000000;
}

img{
/*  position: fixed;
  left: 100px;
  top: 100px;*/
  width: 180px;
  display: inline-block;
  margin: 0 auto;
}

.game-board, .game-info {  
  margin: 0 50px; 
  display: inline-block;
}

.game {
  width: 100%;  
  display: flex;
  flex-direction: column; 
}

.main-field { 
  margin: 0 auto;
  text-align: center;    
}








